.icon-icon-quotes {
  width: 1.23em;
  height: 1em;
  fill: #efefef;
}

.icon-logo-android {
  width: 0.89em;
  height: 1em;
  fill: #A4C639;
}

.icon-logo-apple {
  width: 0.81em;
  height: 1em;
  fill: #FFF;
}

.icon-logo-htmlacademy {
  width: 0.79em;
  height: 1em;
  fill: initial;
}

.icon-logo-icon-editor-contrast {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-logo-icon-editor-crop {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-logo-icon-editor-fill {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-logo-icon-facebook {
  width: 0.46em;
  height: 1em;
  fill: initial;
}

.icon-logo-icon-twitter {
  width: 1.19em;
  height: 1em;
  fill: initial;
}

.icon-logo-icon-youtube {
  width: 1.05em;
  height: 1em;
  fill: initial;
}

.icon-logo-microsoft {
  width: 1.05em;
  height: 1em;
  fill: #F05423;
}

@font-face {
  font-family: streamster;
  src: url("../fonts/streamster.woff2") format("woff2"), url("../fonts/streamster.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: roboto-regular;
  src: url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: roboto-regular;
  src: url("../fonts/roboto-bold.woff2") format("woff2"), url("../fonts/roboto-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: roboto-regular;
  src: url("../fonts/roboto-thin.woff2") format("woff2"), url("../fonts/roboto-thin.woff") format("woff");
  font-weight: 300;
  font-style: normal;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  font-family: roboto-regular, sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 1.5;
  color: #393939;
}

.container {
  max-width: 1325px;
  margin: 0 auto;
}

@media screen and (max-width: 1400px) {
  .container {
    margin: 0 37px;
  }
}

.slick-list,
.slick-track,
.slick-slide,
.slick-slide > div {
  height: 100%;
}

.section {
  padding-bottom: 40px;
  background-color: #e9edee;
}

.section--first {
  padding-top: 40px;
}

@media screen and (max-width: 600px) {
  .section--two, .section--table {
    display: none;
  }
}

.section__wrap {
  display: flex;
  flex-wrap: wrap;
}

@media screen and (max-width: 1400px) {
  .section__wrap {
    display: block;
  }
}

.section__item-v1 {
  flex-basis: 375px;
  height: 530px;
  margin-right: 40px;
}

.section__item-v2 {
  flex-basis: 200px;
  flex-grow: 1;
}

.section__item-v3 {
  flex-basis: 820px;
  margin-right: 40px;
}

.section__item-v4 {
  height: 570px;
  margin-bottom: 40px;
}

.section__item-v4:last-child {
  height: 500px;
  margin-bottom: 0;
}

.section__item-v5 {
  flex-basis: 727px;
  margin-right: 40px;
}

@media screen and (max-width: 1400px) {
  .section__item-v1, .section__item-v2, .section__item-v3, .section__item-v4, .section__item-v5 {
    width: 100%;
    margin-bottom: 40px;
  }
}

.chart {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 30px;
  background-color: #f3f3f3;
}

.chart__head {
  display: flex;
  align-items: center;
  margin-bottom: 50px;
}

.chart__title {
  margin: 0;
  padding: 0;
  margin-right: auto;
  font-size: 33px;
  font-weight: 400;
  color: #a3a3a3;
}

.chart__main {
  text-align: center;
  margin-bottom: 40px;
}

.chart__main--col {
  display: flex;
}

@media screen and (max-width: 680px) {
  .chart__main--col {
    flex-direction: column-reverse;
  }
}

.chart__main--diagram {
  margin-left: auto;
}

@media screen and (max-width: 680px) {
  .chart__main--diagram {
    margin-right: auto;
    margin-bottom: 20px;
  }
}

.button {
  width: 36px;
  height: 36px;
  margin-left: 5px;
  border: 2px solid #a3a3a3;
  border-radius: 12px;
  outline: 0;
  background-color: transparent;
  cursor: pointer;
}

.button--menu {
  background: url(../img/icon-menu.png) center center no-repeat;
}

.button--options {
  background: url(../img/icon-options.png) center center no-repeat;
}

.more {
  font-size: 17px;
  font-weight: 300;
  text-decoration: none;
  color: #858585;
}

.sale__count {
  font-size: 100px;
  font-weight: 700;
  color: #f95738;
}

.sale span {
  display: inline-block;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 0 18px 30px 18px;
  border-color: transparent transparent #f95738 transparent;
}

.sale__more {
  font-size: 25px;
  color: #858585;
  text-decoration: none;
}

.sale__more:hover {
  text-decoration: underline;
}

.total {
  font-size: 38px;
  font-weight: 400;
  margin-top: 40px;
  text-align: left;
  color: #363636;
}

.header__wrap {
  position: relative;
  display: flex;
  align-items: center;
}

.header__logo {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-basis: 245px;
  height: 72px;
  margin-right: 20px;
  color: #fff;
  background-color: #3498db;
}

.header__logo:before {
  content: '';
  position: absolute;
  top: 0;
  right: 100%;
  display: block;
  width: 50vw;
  height: 100%;
  background-color: #3498db;
}

.header__search {
  width: 350px;
  margin-right: 20px;
}

@media screen and (max-width: 820px) {
  .header__search {
    display: none;
  }
}

.header__face {
  position: absolute;
  top: 20px;
  right: 0;
  z-index: 10;
  margin-left: auto;
}

@media screen and (max-width: 1000px) {
  .header__face {
    display: none;
  }
}

.logo {
  font-family: streamster;
  font-weight: normal;
  display: block;
  font-size: 33px;
  line-height: 1;
  color: inherit;
  text-decoration: none;
}

.search {
  position: relative;
}

.input--search {
  width: 100%;
  height: 37px;
  padding: 0 10px;
  outline: 0;
  border: 0;
  font-size: 13px;
  line-height: 37px;
  background-color: #e3e3e3;
  border-radius: 10px;
}

.input--submit {
  position: absolute;
  top: 10px;
  right: 20px;
  width: 16px;
  outline: 0;
  border: 0;
  background: url(../img/search.png) center center no-repeat;
  background-size: contain;
  opacity: .4;
}

.meta__item {
  display: inline-block;
  width: 20px;
  height: 17px;
  margin-right: 10px;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  opacity: .3;
}

.meta__item--email {
  background-image: url(../img/mail.png);
}

.meta__item--map {
  background-image: url(../img/map.png);
}

.meta__item--cart {
  background-image: url(../img/cart.png);
}

.face {
  overflow: hidden;
  width: 106px;
  height: 106px;
  border-radius: 50%;
  background-image: linear-gradient(0deg, #d2d6da 0, #d2d6da 50%, #fff 50%, #fff);
}

.face__img {
  overflow: hidden;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 98px;
  height: 98px;
  border-radius: 50%;
}

.nav {
  background-color: #2c3e50;
}

.nav__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 0;
  margin: 0;
}

@media screen and (max-width: 1000px) {
  .nav__list {
    justify-content: center;
  }
}

.nav__item {
  position: relative;
  overflow: hidden;
  margin-right: 40px;
  padding: 15px 0;
  font-size: 25px;
  list-style: none;
  color: #fff;
}

.nav__item:before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translateY(20px);
  display: block;
  width: 100%;
  height: 7px;
  background: #f95738;
  transition: transform 0.3s ease;
}

.nav__item--is-active:before, .nav__item:hover:before {
  transform: translateY(0);
}

.nav a {
  padding-left: 30px;
  text-decoration: none;
  color: inherit;
  background-repeat: no-repeat;
  background-position: left center;
}

.diagram-circle {
  position: relative;
}

.diagram-circle__text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 50px;
  font-weight: 700;
  color: #f95738;
}

.diagram-circle__svg svg {
  width: 230px;
  height: 230px;
  transform: rotate(-90deg);
  border-radius: 50%;
}

.diagram-circle__svg--sector svg {
  border: 8px solid #000;
}

.diagram-circle__svg--many {
  position: relative;
  width: 230px;
  height: 230px;
  transform: rotate(-90deg);
}

.diagram-circle__svg--many svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.diagram-circle__svg--many svg:nth-child(2n) {
  width: 200px;
  height: 200px;
}

.diagram-circle__svg--many svg:nth-child(3n) {
  width: 170px;
  height: 170px;
}

.circle__back {
  fill: transparent;
  stroke: #e4e4e4;
  stroke-width: 4;
  stroke-linecap: round;
}

.circle__front {
  fill: transparent;
  stroke: #f95738;
  stroke-width: 4;
  stroke-linecap: round;
}

.circle__thin {
  fill: transparent;
  stroke-width: 1;
  stroke-linecap: round;
}

.circle__round {
  fill: transparent;
  stroke-width: 32;
  stroke-linecap: butt;
}

.line-compare__item {
  margin-bottom: 20px;
}

.line-compare__title {
  margin-bottom: 10px;
  font-size: 25px;
  text-align: left;
  color: #858585;
}

.line-compare__legend {
  margin-top: 40px;
}

.line {
  height: 15px;
  background: #2c3e50;
  position: relative;
}

.line:after {
  content: '';
  position: absolute;
  top: 0;
  left: calc(100% - 7.5px);
  display: block;
  width: 15px;
  height: 15px;
  background: inherit;
  border-radius: 50%;
}

.line--second {
  position: relative;
  top: -3.75px;
  background-color: #f95738;
}

.legend {
  text-align: left;
}

.legend__item {
  display: inline-block;
  margin-right: 20px;
}

.legend__item--vert {
  display: block;
  padding: 5px 0;
  border-bottom: 1px solid #dedede;
}

.legend__color {
  display: inline-block;
  width: 18px;
  height: 18px;
  margin-right: 10px;
}

.legend__title {
  display: inline-block;
  font-size: 25px;
  font-weight: 300;
  color: #858585;
}

.rating-table {
  width: 100%;
  table-layout: fixed;
  border-collapse: collapse;
  font-size: 25px;
  font-weight: 300;
  color: #858585;
}

.rating-table thead {
  font-weight: 400;
  text-align: center;
}

.rating-table thead tr:nth-child(odd) {
  background-color: inherit;
}

.rating-table tr:nth-child(odd) {
  background-color: #ecf0f1;
}

.rating-table tr td {
  padding: 25px 0;
  border-bottom: 1px solid #353535;
}

.rating-table tr td:first-child {
  text-align: left;
  padding-left: 20px;
}

@media screen and (max-width: 1400px) {
  .rating-table tr td:first-child {
    display: none;
  }
}

@media screen and (max-width: 1400px) {
  .rating-table tr td:nth-child(3n) {
    display: none;
  }
}

.rating-table__id, .rating-table__sale, .rating-table__rating {
  width: 180px;
}

.rating {
  width: 150px;
  height: 27px;
  margin: 0 auto;
  background: url(../img/star.png) left top repeat-x;
}

.rating__active {
  background: url(../img/star.png) left center repeat-x;
  height: 100%;
}
