@include font(streamster, streamster);
@include font(roboto-regular, roboto-regular);
@include font(roboto-regular, roboto-bold, 700);
@include font(roboto-regular, roboto-thin, 300);


@mixin str {
  font-family: streamster;
  font-weight: normal; }

@mixin rob {
  font-family: roboto-regular, sans-serif;
  font-weight: normal; }

@mixin robb {
  font-family: roboto-regular, sans-serif;
  font-weight: 700; }

@mixin robt {
  font-family: roboto-regular, sans-serif;
  font-weight: 300; }

